import { Platform, BuildType } from '../../../constants';
import { types } from '../../../buildConfig';
import { readFileList } from '../../../utils/file';

import { GenericAndroidJob } from './job';

async function prepareJob(
  config: types.GenericAndroidBuildConfig,
  projectUrl: string,
  projectDir: string,
): Promise<GenericAndroidJob> {
  return {
    platform: Platform.Android,
    projectUrl,
    type: BuildType.Generic,
    secrets: {
      files: config.secretFiles ? await readFileList(config.secretFiles, projectDir) : [],
    },
    nativeProjectDirectory: 'android',
    gradleCommand: ':app:assembleRelease',
    artifactPath: 'android/app/build/outputs/apk/release/app-release.apk',
  };
}

export default prepareJob;
